@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400&display=swap);
body {
  margin: 0;
  font-family: "Open Sans", sans-serif !important;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 24px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

img {
  max-width: 100%;
  height: auto;
}

h1 {
  font-size: 60px;
  font-weight: 300;
  line-height: 1;
}

h2 {
  font-size: 48px;
  font-weight: 300;
  line-height: 1;
}

h3 {
  font-size: 30px;
  font-weight: 600;
  line-height: 1.5;
}

a {
  text-decoration: none;
}

a:hover {
  color: #C00000;
  text-decoration: none;
  cursor: pointer;
  transition: 0.3s;
}

ul {
    list-style-type: none;
}

.content-wrapper {
  padding: 64px 20%;
}

.primary-image {
  border-bottom: 1px solid #cccccc;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  flex-flow: row wrap;
}

@media only screen and (max-width: 1080px) {
  .content-wrapper {
    padding: 64px 72px;
  }
}
.ngc-button {
  border: 0;
  cursor: pointer;
  display: inline-block;
  line-height: 1;
}

.ngc-button--primary {
  color: white;
  width: auto;
  height: 220px;
  flex-grow: 1;
  flex: 1 1;
  display: flex;
  flex-direction: row;
  padding-right: 24px;
}

.ngc-button--secondary {
  color: black;
  font-size: 20px;
  background-color: transparent;
}

.ngc-button--primary svg {
  margin-top: 47px;
}

.ngc-button--primary p {
  line-height: 1.4;
  margin-top: 41px;
  margin-left: 32px;
  font-size: 30px;
}

.ngc-button--secondary-dark {
  color: white;
}

.ngc-button--secondary.selected {
  color: #C00000;
}

.ngc-button--secondary-dark.selected {
  color: #ff2929;
}
.ngc-logo-bordered {
    border: 1px solid white;
    padding: 1.4em;
}
.wrapper{display:flex;align-items:center;justify-content:space-between}.wrapper .nav-items{display:flex;align-items:center;justify-content:space-between}.wrapper .nav-items li+li{margin-left:20px;cursor:pointer}.wrapper .overflow{position:absolute;background:#c00000;border-radius:8px}.wrapper .overflow a{padding:20px 30px;color:white !important}.wrapper .close{display:none}.wrapper-collapsed .close{display:block}.wrapper-collapsed a{color:white}.wrapper-collapsed a:hover,.wrapper-collapsed span:hover{color:black !important}.wrapper-collapsed .nav-items{position:fixed;background-color:#c00000e0;color:white;top:0;right:0;margin:0;padding:1.5em 2.5em;height:100%;width:60%}.wrapper-collapsed .nav-items li{padding:1.5em 0;border-top:1px solid #ffffff38}.wrapper-collapsed .nav-items li:first-child{border-top:0px !important}.wrapper-collapsed .overflow{position:relative;background:transparent}.wrapper-collapsed .overflow a{padding:1.5em 2em !important}.wrapper-collapsed .overflow a:last-child{padding-bottom:0 !important}.wrapper,.wrapper-collapsed{border-bottom:1px solid #cccccc;padding:36px 140px;align-items:center}.wrapper .close,.wrapper-collapsed .close{width:1.5em;height:1.5em;position:fixed;top:0;right:0;margin:2em;cursor:pointer;-webkit-transform:translate(-50%, 0%);transform:translate(-50%, 0%)}.wrapper .close:after,.wrapper-collapsed .close:after{content:'';height:1.5em;border-left:0.2em solid #fff;position:absolute;-webkit-transform:rotate(45deg);transform:rotate(45deg);left:28px}.wrapper .close:before,.wrapper-collapsed .close:before{content:'';height:1.5em;border-left:0.2em solid #fff;position:absolute;-webkit-transform:rotate(-45deg);transform:rotate(-45deg);left:28px}.wrapper .hamburger,.wrapper-collapsed .hamburger{position:absolute;right:0;width:1.5em;height:0.8em;margin:1em 2em;cursor:pointer;border-top:0.2em solid rgba(0,0,0,0.6);border-bottom:0.2em solid rgba(0,0,0,0.6)}.wrapper .hamburger:before,.wrapper-collapsed .hamburger:before{content:'';position:absolute;top:0.3em;left:0px;width:100%;border-top:0.2em solid rgba(0,0,0,0.6)}.wrapper *,.wrapper-collapsed *{transition:all 200ms linear}.wrapper img,.wrapper-collapsed img{display:inline-block}.wrapper .nav-items,.wrapper-collapsed .nav-items{font-size:20px}.wrapper .nav-items li,.wrapper-collapsed .nav-items li{cursor:pointer}.wrapper .nav-items li:hover .overflow,.wrapper-collapsed .nav-items li:hover .overflow{display:flex}.wrapper .nav-items .overflow,.wrapper-collapsed .nav-items .overflow{display:none;justify-content:space-between;flex-direction:column}.wrapper .nav-items .overflow a,.wrapper-collapsed .nav-items .overflow a{padding:20px 30px;color:white !important}.wrapper .nav-items .overflow a:hover,.wrapper-collapsed .nav-items .overflow a:hover{color:black !important}.wrapper .nav-items .overflow a+a,.wrapper-collapsed .nav-items .overflow a+a{border-top:1px solid #ffffff38}@media only screen and (max-width: 1080px){.wrapper,.wrapper-collapsed{padding:36px 50px}}@media only screen and (max-width: 430px){.wrapper,.wrapper-collapsed{padding:32px 32px}}

footer {
  font-size: 20px;
  color: white;
  background-color: black;
  padding: 80px 140px;
}

.ngc-footer-top {
    display: flex;
    flex-flow: row wrap;
    align-items: center;
}

.ngc-footer-bottom {
    display: flex;
    flex-flow: row wrap;
    justify-content: space-between;
    align-items: center;
    margin-top: 48px;
    font-size: 16px;
}

.ngc-footer-bottom .locations {
    display: flex;
    flex-flow: row wrap;
    line-height: 1.6;
}

.ngc-footer-bottom .locations div, .ngc-footer-bottom div p {
    margin: 10px 80px 10px 0;
}

@media only screen and (max-width: 1024px) {
    footer {
        padding: 48px 48px;
    }
}

@media only screen and (max-width: 768px) {
    footer {
        padding: 26px 48px;
    }
}
.cta-wrapper {
  border-bottom: 1px solid #cccccc;
  display: flex;
  flex-flow: row wrap;
  align-items: center;
  justify-content: space-between;
}

.cta-wrapper h1 {
  margin-left: 140px;
  flex: 2 1;
}

@media only screen and (max-width: 1080px) {
  .cta-wrapper h1 {
    margin-left: 50px;
  }
}

@media only screen and (max-width: 800px) {
  .cta-wrapper h1 {
    margin-left: 50px;
    font-size: 1.8em !important;
  }
}

.primary-image > .right {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  flex: 1 1;
}

.primary-image > img {
  margin: 0 62px;
}

.primary-image > .right > img {
  margin: 0 72px;
}

.primary-image > .right > div {
  padding: 106px 72px;
  font-size: 30px;
  text-align: center;
  background-color: #c00000;
  color: white;
}

.wrapper-values {
  display: flex;
  flex-flow: row wrap;
  grid-template-columns: repeat(3, 1fr);
}

.wrapper-values > div:first-child {
  background-color: #c00000;
  color: white;
}

@media only screen and (max-width: 800px) {
  .primary-image > .right > img {
    margin: 0 16px;
  }
  .primary-image > .right > div {
    padding: 42px 42px;
    font-size: 20px;
  }

  .primary-image > .right > div {
    text-align: start !important;
  }
}

@media only screen and (max-width: 1080px) {
  .primary-image > .right > div {
    text-align: justify !important;
  }
}

.value-item{border-top:1px solid #cccccc;padding:80px 85px;transition:0.3s;flex:1 1 20%}.value-item .value-icon{position:relative;width:150px;height:150px;border:1px solid #b3aab0;background-color:white;border-radius:100%;float:right;top:-150px;display:flex;align-items:center}.value-item .value-icon img{width:75%;margin:auto;height:auto}.value-item+.value-item{border-left:1px solid #cccccc}.value-item.first{background-color:transparent !important;color:black !important}.value-item:hover{background-color:#c00000;color:white}@media only screen and (max-width: 1080px){.value-item{padding:62px 72px;flex:1 1 30%}.value-icon{top:20px !important;width:80px !important;height:80px !important}}@media only screen and (max-width: 800px){.value-item{padding:12px 48px;flex:1 1 50%}}

.network-item{display:flex;flex-flow:column;padding:32px 100px 100px 100px;background-color:white;-webkit-filter:drop-shadow(0 1px 10px rgba(0,0,0,0.156863));filter:drop-shadow(0 1px 10px rgba(0,0,0,0.156863));transition:0.3s}.network-item img{padding:0 92px;width:70%;height:auto}.network-item .img-c{flex:1 1;display:flex;align-items:center}.network-item .content{flex:2 1}.network-item p{font-size:20px}.network-item--small{display:block;padding:38px}.network-item--small .img-c{width:100px;height:100px;border-radius:100%;border:1px solid black;margin:0 auto}.network-item--small .img-c img{width:60%;height:60%;padding:20%}.network-item--small .content h3{font-size:20px;text-align:center}.network-item--small p{text-align:center;font-size:16px}.network-item:hover{-webkit-filter:drop-shadow(0 1px 20px rgba(0,0,0,0.156863));filter:drop-shadow(0 1px 20px rgba(0,0,0,0.156863));z-index:99}@media only screen and (max-width: 1080px){.network-item{padding:72px !important;flex:1 1 50%  !important}.network-items{padding:0 !important}}

.network-items{display:flex;flex-flow:row wrap;grid-template-columns:repeat(2, 1fr);padding:80px 100px}.network-items .network-item{margin:0 20px;margin-bottom:40px;flex:0 0 35%}

.process-items{display:flex;flex-flow:row wrap;grid-template-columns:repeat(3, 1fr)}.process-items .network-item--small{margin-bottom:40px;flex:1 1 25% !important}.process-items .network-item--small:nth-child(even){margin-left:20px;margin-right:20px}

.language-select{position:fixed;background-color:#f3f3f3;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;-webkit-transform:translate(0, -50%);transform:translate(0, -50%);top:50%;right:0}.language-select li{margin:0 auto;padding:0;font-size:0}.language-select ul{margin:0;padding:0.4em}.language-select img{width:72px}

